.section-misc {
  /* position: relative; */
  width: 100%;
  /* padding-top: 20px; */
  margin-top: -20px; /* TODO: debug this */
  padding-bottom: 50px;
  /* padding-top: 20px; */
  background-color: #242526;
}
.section-misc .wrapper {
  position: relative;
  width: 80vw;
  left: 50%;
  transform: translateX(-50%);
  padding-top: 20px; /* TODO: debug this */
}
.section-misc .line {
  margin-top: 20px;
  color: white;
  text-align: center;
}
.section-misc .notification {
  height: 50px;
  text-align: center;
  color: white;
}
.section-misc .notification .title {
  font-size: 15px;
  font-weight: 500px;
  padding-bottom: 10px;
}
.section-misc .notification .consent {
  font-size: 12px;
  font-weight: 300px;
}
.section-misc .team-info {
  font-weight: 300;
  color: white;
  line-height: 1.5;
  text-align: center;
}
.section-misc .team-info .title {
  font-size: 15px;
  font-weight: 500px;
  padding-bottom: 10px;
}
.section-misc .team-info .section-team-member {
  font-size: 12px;
  font-weight: 300px;
}
.section-misc .team-info .team-member {
  padding-left: 10px;
  padding-right: 10px;
  display: inline-block; 
}
.section-misc .partner-info {
  color: white;
  text-align: center;
  padding-bottom: 50px;
}
.section-misc .partner-info .title {
  font-weight: 500px;
  font-size: 15px;
  padding-bottom: 10px;
}
.section-misc .partner-info .name {
  font-size: 12px;
  font-weight: 300px;
}

@keyframes hello {
	0% {transform: rotate(0deg);}
	10% {transform: rotate(-10deg);}
	20% {transform: rotate(12deg);}
	30% {transform: rotate(-10deg);}
	40% {transform: rotate(9deg);}
	50% {transform: rotate(0deg);}
	100% {transform: rotate(0deg);}
}

